import { Observable } from 'rxjs/Observable';
import { Injectable } from '@angular/core';
import { Action } from '@ngrx/store';
import { Actions, Effect } from '@ngrx/effects';
import { HttpClient } from '@angular/common/http';
import { of } from 'rxjs/observable/of';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/mergeMap';

import {
    UpdateParentDataAction,
    GetChildDataAction,
    GET_ACCOUNT_PARENT
} from '../actions';

import { AssetDataService } from '../services';

@Injectable()
export class ParentDataEffect {
    @Effect()
    update$: Observable<any> = this.actions$
        .ofType(GET_ACCOUNT_PARENT)
        .switchMap(() =>
            this.assetService
                .getParent()
                .mergeMap(data => {
                    if(data) {
                        return [
                            new UpdateParentDataAction(data),
                            new GetChildDataAction(data.guid)
                        ];
                    }
                })
        );

    constructor(
        private assetService: AssetDataService,
        private actions$: Actions
    ) {}
}


