import { Component} from '@angular/core';
import { createNode } from '../../constant';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AssetDataService } from '../../services';
import * as fromRoot from '../../reducers';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs/Observable';
import { ExpandNode } from '../../models';

@Component({
  selector: 'create-node',
  templateUrl: './createNode.component.html',
  styleUrls: ['./createNode.component.css']
})
export class CreateNodeComponent {
  public currentNode$: Observable<ExpandNode>;
  public parentNode$;
	public NodeList = [];
	public name;
	public NodeId;
	public tokenId;
  public nodeName;
  public AssetName;
  public SerialNumber;
  public selectedNode;
	public submitted = false;
  public FormNodeData =[];
	myForm: FormGroup;
  AddClass:FormGroup;

	constructor(
    public store: Store<fromRoot.State>,
    private fb: FormBuilder,
    private assetService: AssetDataService
    )
  {
		this.createForm();
		this.NodeList = createNode;
    this.currentNode$ = this.store.select(fromRoot.getExpandResult);
    this.parentNode$ = this.store.select(fromRoot.getParentData);
	}
	createForm() {
    this.myForm = this.fb.group({
       'name':'',
       'NodeId':'',
       'tokenId':'',
    });
    this.AddClass = this.fb.group({
       'nodeName':'',
       'AssetName':'',
       'SerialNumber':''
    })
    }
    createPrincipal(post) {
    this.name = post.name;
    this.NodeId = post.NodeId;
    this.tokenId = post.tokenId;
     console.log("myform:-" ,this.myForm.value);
     this.submitted = !this.submitted;
  //    if (this.myForm.valid) {
  //    console.log("Form Submitted!");
  // }
 }
  resetForm(){
     this.myForm.reset();
  }

  SelectedNodeType(node){
    // this.FormNodeData = 
   const clicked = true;
   this.selectedNode = node.name;
  }

   createOrg(post){
    this.nodeName = post.nodeName;
    this.SerialNumber = post.SerialNumber;
    this.AssetName =post.AssetName;
     console.log("addclass:-" ,this.AddClass.value);
    this.AddClass.reset();
  }
}



