import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import * as _ from 'lodash';
import {
    ENVIRONMENT,
    ChildClasses
  } from '../constant';
import 'rxjs/add/operator/map';

import { ChildData } from '../models';

@Injectable()
export class ClassDataService {
    constructor(private http: HttpClient) {}

    getChildClasses(id: string): Observable<ChildData[]> {
        return this.http.get<any>(ChildClasses).map(data => {
            const x = data.result.map(o => {
                return {
                    name: o.name,
                    guid: o.guid,
                    label: o.label
                }
            });
            // const d = {
            //         name: 'child-data-class',
            //         guid: 'child-data-class',
            //         label: 'child-data-class'
            //     }
            // console.log('class result:-',data);
            return x;
        });

    }
}



