import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import * as _ from 'lodash';
import {
    ENVIRONMENT,
    SearchAsset,
    ParentApi,
    ChildClasses
  } from '../constant';
import 'rxjs/add/operator/map';

import { SearchNode, ParentData } from '../models';

@Injectable()
export class AssetDataService {
    constructor(private http: HttpClient) {}

    getAssetByName(): Observable<SearchNode[]> {
        return this.http.get<any>(SearchAsset).map(data => {
            const x = data.result.map(o => {
                return {
                    name: o.name,
                    guid: o.guid,
                    label: o.label,
                    view: true
                }
            });
            console.log('getasset by name:-',data);
            console.log('returndata:-', x);
            return x;
        });

    }

    getParent(): Observable<ParentData> {
        return this.http.get<any>(ParentApi).map(data => {
            return data.result.class[0];
        });
    }

    getChildClass(): Observable<SearchNode[]> {
        return this.http.get<any>(ChildClasses).map(data => {
            const x = data.result.map(o => {
                return {
                    name: o.name,
                    guid: o.guid,
                    label: o.label,
                    view: true
                }
            });
            console.log('getparent by name:-',data);
            console.log('returndata:-', x);
            return x;
        });
    }
}



